/* ============ Account  ============*/
:root {
    /* Biến màu sắc của web */
    --primary-color: #00483d;
    --primary-bg: #f4f4f4;
    --price-color: #fd475a;
    --black: black;
    --white: white;
    --background: #f6f6f6;
    --gray: #4e4e4e;
    --orange: #f25c05;
    --green: #009981;
    --border: #d8d5d5;
    --title-hover: #009a82;

    /*Biến font size */
    --heading: 2rem;
    --subHeading: 1.6rem;
    --bodyText: 1.3rem;
    --inputField: 1.2rem;
    --priceText: 1.4rem;
}

/* Common  */
#form_reg input[type="text"],
#form_reg input[type="email"],
#form_reg input[type="password"],
#form_login input[type="text"],
#form_login input[type="password"] {
    width: 100%;
    box-sizing: border-box;
    height: 39px;
    margin: 4px 0;
    padding: 0 10px;
    border: 1px solid var(--primary-color);
    border-radius: 5px;
}

.form__account {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 30px 0;
    height: 630px;
    background-color: var(--white);
    border-radius: 10px;
    box-shadow: 1px 2px 15px 2px rgba(0, 0, 0, 0.3);
}

.sidebar__user,
.account__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    height: 100%;
}

.btn.user__cta {
    margin-top: 20px;
    border: 2px solid var(--white);
    padding: 14px 37px;
    border-radius: 10px;
}

.btn.user__cta:hover {
    color: var(--price-color);
    border: 2px solid var(--price-color);
    background: var(--white);
}

/* ============ Login  ============*/

.heading-title {
    font-size: 3.6rem;
    font-weight: 900;
    margin-bottom: 30px;
}

.form__group {
    text-align: left;
    margin: 20px;
}

.control__reset {
    padding-left: 90px;
}

.account__inner {
    flex-basis: 65%;
}

.sidebar__user {
    flex-basis: 35%;
    background: #f12a43;
    color: var(--white);
    border-radius: 0 10px 10px 0;
}

.sidebar__user--heading {
    font-size: 3rem;
}

.sidebar__user--desc {
    padding: 30px 0;
}

/* ============ Register  ============*/
/* #form_reg {
    width: 360px;
} */

#form_reg .form__group {
    text-align: left;
    margin: 8px;
}

.row-form__group {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.row-form__group .form__group {
    width: 50%;
    text-align: left;
}

/* ============ Update Account  ============*/

.form__group .image__user {
    display: inline-block;
    margin-bottom: 10px;
}

#form_login input[type="file"] {
    width: 100%;
}

.avatar__user {
    height: 200px;
    width: 200px;
    border-radius: 50%;
    background: var(--white);
    overflow: hidden;
}

.avatar__user img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.subtitle__user {
    margin-top: 20px;
    font-size: 2.8rem;
    color: var(--white);
}
/* ============ Reset password Account  ============*/
.newPassword .form__group,
.resetPassword .form__group {
    width: 340px;
}

.resetOkPassword .heading-title {
    width: 410px;
    line-height: 1.7;
    color: var(--orange);
}
